import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { ReactiveFormsModule, FormsModule } from "@angular/forms";

// comlists
import { LessonPlanCreateComponent } from './lesson-plan-create/lesson-plan-create.component';
import { LessonPlanListsComponent } from './lesson-plan-lists/lesson-plan-lists.component';

// internal Modules
import { BasicComponentsModule } from "@basic-components/basic-components.module";
import { Role } from "@enum/app_enum";
import { AuthGuardService } from "@core/guards/auth-guard.service";

const comLists=[
  LessonPlanListsComponent,
  LessonPlanCreateComponent,
];

const routes: Routes = [
  {
    path: 'management',
    component: LessonPlanListsComponent,
    canActivate: [AuthGuardService],
    data: { roles: [Role.Admin,Role.Teacher,Role.StudentAffair] }
  },
  {
    path: 'create',
    component: LessonPlanCreateComponent,
    canActivate: [AuthGuardService],
    data: { roles: [Role.Teacher] }
  },
  {
    path: 'management/edit/:id',
    component: LessonPlanCreateComponent,
    canActivate: [AuthGuardService],
    data: { roles: [Role.Teacher] }
  }
];

@NgModule({
  declarations: [comLists],
  imports: [
    CommonModule,
    BasicComponentsModule,
    RouterModule.forChild(routes),
    ReactiveFormsModule,
    FormsModule
  ],
  exports:[
    comLists
  ]
})
export class LessonplanModule { }
