import { Component, OnDestroy, OnInit } from '@angular/core';
import { TeacherSideMenu, Label, ActionLabel } from "@enum/app_enum";
import { LessonplanService } from "@core/services/serviceIndex";
import { Subscription } from 'rxjs';
@Component({
  selector: 'app-lesson-plan-lists',
  templateUrl: './lesson-plan-lists.component.html',
  styleUrls: ['./lesson-plan-lists.component.css']
})
export class LessonPlanListsComponent implements OnInit,OnDestroy {
  public routeDirLists = {
    routeName: [TeacherSideMenu.LessonPlan],
    routeLink: []
  };
  public tableHeader = [Label.name, Label.createDate, Label.lastModified, Label.fileSize, Label.action];
  public actionBtns = [ActionLabel.edit, ActionLabel.download, ActionLabel.delete];
  public dataLists: any = {
  };
  public objectLabel: any = ["name", "createdDate", "updatedDate", "fileSize"];

  public limit: any = 10;
  public page: any = 1;
  public totalCount: any;
  public filter: any = {
    sortColumn: this.objectLabel[0],
    sortDirection: "asc",
    index: 0
  };
  public actionLinks: any = {
    editLink: "teacher/lesson-plans/management/edit",
    deleteLink: "lesson-plans",
    idKeyname: "lessonPlanId",
  }
  public searchData: any;
  public subscription: Subscription;

  constructor(private _service: LessonplanService) { }

  ngOnInit(): void {
    this.getLessonList();

  }

  pageChanges(e: any) {
    console.log(e);
    this.page = e;
    this.getLessonList();
  }

  onsearch(search: string) {
    console.log(search);
    this.searchData = search;
    this.getLessonList();
  }

  onFilter(filter: any) {
    this.filter = filter;
    this.getLessonList();
  }

  getLessonList() {
    this.subscription = this._service.getLessonList(this.searchData, this.page, this.limit, this.filter.sortColumn, this.filter.sortDirection)
      .subscribe((res) => {
        this.dataLists.data = res.lessonPlans;
        this.totalCount = res.totalCount;
        this.dataLists.tableHeader = this.tableHeader;
        this.dataLists.actionBtns = this.actionBtns;
        this.dataLists.objectLabel = this.objectLabel;
        this.dataLists.btnLink = this.actionLinks;
      })
  }

  ngOnDestroy(){
    this.subscription.unsubscribe();
  }
}
