import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { ActivatedRoute, Router } from '@angular/router';

import { LessonplanService } from "@core/services/lessonplan.service";
import { TeacherSideMenu, ActionLabel, EssentialRoute } from "@enum/app_enum";
import { LessonPlan } from "@model/lessonPlan";
import { AuthenticationService } from "@core/authentication/authentication.service";
import { Subscription } from 'rxjs';
@Component({
  selector: 'app-lesson-plan-create',
  templateUrl: './lesson-plan-create.component.html',
  styleUrls: ['./lesson-plan-create.component.css']
})
export class LessonPlanCreateComponent implements OnInit, OnDestroy {
  public routeDirLists: any = {
    routeName: [TeacherSideMenu.LessonPlan],
    routeLink: [EssentialRoute.LessonList]
  };
  public submitted: boolean;
  public lessonplanForm: FormGroup;
  public file: any;
  public lessonPlanId: any;
  public subscription: Subscription;


  constructor(private formBuilder: FormBuilder, private _ser: LessonplanService,
    private _activeRoute: ActivatedRoute, private _auth: AuthenticationService, private _router: Router) { }

  ngOnInit(): void {
    this._activeRoute.params.subscribe(params => {
      this.lessonPlanId = params.id;
      this.lessonplanForm = this.formBuilder.group({
        name: ["", [Validators.required]],
      });
      if (this.lessonPlanId) {
        this.getLessonPlanDetail();
        this.routeDirLists.routeName.push(ActionLabel.edit);
      } else this.routeDirLists.routeName.push(ActionLabel.create)
    });
  }

  getLessonPlanDetail() {
    this.subscription = this._ser.getLessonPlanDetail(this.lessonPlanId)
      .subscribe((res) => {
        let temp = res.lessonPlan;
        this.lessonplanForm = this.formBuilder.group({
          name: [temp.name, [Validators.required]],
        });
      })
  }

  onSubmit() {
    this.submitted = true
    if (this.lessonplanForm.invalid || !this.file) return;
    else {
      if (!this.lessonPlanId)
        this.onCreate();
      else this.onEdit();
    }
  }

  onCreate() {
    let lessonPlan = new LessonPlan();
    lessonPlan.name = this.lessonplanForm.value.name;
    lessonPlan.lesssonPlan = this.file;
    this.subscription = this._ser.onCreateLessonPlan(lessonPlan)
      .subscribe((res) => {
        this.submitted = false;
      });
  }

  onEdit() {
    let lessonPlan = new LessonPlan();
    lessonPlan.name = this.lessonplanForm.value.name;
    lessonPlan.lesssonPlan = this.file;
    lessonPlan._id = this.lessonPlanId;
    this.subscription = this._ser.onUpdateLessonPlan(lessonPlan)
      .subscribe((res) => {
        this.submitted = false;
      });
  }
  getfile(file: any) {
    this.file = file;
  }

  // convenience getter for easy access to form fields
  get f() { return this.lessonplanForm.controls; }

  cancelLessonPlan() {
    let role = this._auth.currentUserValue.role.toLocaleLowerCase();
    this._router.navigateByUrl(role + "/" + EssentialRoute.LessonList);
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
}
